import React from 'react';
import Header from '../header';
import { useState, useEffect } from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import { Link } from "react-router-dom";
import MovieList from '../compo/movielist/movielist.js'
import { MDBFooter, MDBContainer } from 'mdb-react-ui-kit';


const Home = () => {

  const [data1, setData] = useState([]);
  useEffect(() => {
    fetch("https://api.themoviedb.org/3/movie/popular?api_key=4e44d9029b1270a757cddc766a1bcb63&language=en-US")
      .then((res) => res.json())
      .then(data => setData(data.results))
  }, []);

  return (
    <div className="poster">
      <Carousel
        showThumbs={false}
        autoPlay={true}
        transitionTime={3}
        infiniteLoop={true}
        showStatus={false}
      >
        {
          data1.map(movie => (
            <div style={{ textDecoration: "none", color: "white" }} to={`/movie/${movie.id}`} >          <div className="posterImage">
              <img src={`https://image.tmdb.org/t/p/original${movie && movie.backdrop_path}`} />

            </div>
              <div className="posterImage__overlay">
                <div className="posterImage__title">{movie ? movie.original_title : ""}</div>
                <div className="posterImage__runtime">
                  {movie ? movie.release_date : ""}
                  <span className="posterImage__rating">
                    {movie ? movie.vote_average : ""}
                    <i className="fas fa-star" />{" "}
                    <i className="fas fa-star" />{" "}
                    <i className="fas fa-star" />{" "}
                    <i className="fas fa-star" />{" "}


                  </span>
                </div>
                <div className="posterImage__description">{movie ? movie.overview : ""}</div>
              </div>
            </div>
          ))
        }
      </Carousel>

      <MovieList />


      <MDBFooter bgColor='light' className='text-center text-lg-left'>
        <div className='text-center p-3' >
          &copy; {new Date().getFullYear()} Copyright:{' '}

          <a className='text-light' target='_blank' href='https://github.com/thaha1233' >
            Design By Taha Ik
          </a>
        </div>
      </MDBFooter>



    </div>


  );

}

export default Home;
